"use client";

import React, { useState } from "react";
import Roulette from "@/app/components/Roulette";
import ResultModal from "@/app/components/ResultModal";
import { SettingsIcon } from "@/app/components/icons";
import { DEFAULT_QUESTIONS } from "../../constants";

export default function Page() {
  const [questions] = useState<string[]>(DEFAULT_QUESTIONS.slice(0, 10)); // 10å€‹ã ã‘ä½¿ç”¨
  const [isSpinning, setIsSpinning] = useState(false);
  const [result, setResult] = useState<string | null>(null);
  const [isResultOpen, setIsResultOpen] = useState(false);

  const handleStart = () => {
    if (isSpinning) return;
    setIsSpinning(true);
    setResult(null);
  };

  const handleStop = (topQuestion: string) => {
    setResult(topQuestion);
    setIsSpinning(false);
    setIsResultOpen(true);
  };

  return (
    <div className="min-h-screen bg-gradient-to-b from-pink-100 to-blue-100 text-gray-800 flex flex-col items-center justify-center p-6 relative">
      {/* ã‚¿ã‚¤ãƒˆãƒ« */}
      <header className="text-center mb-6">
        <h1 className="text-4xl font-bold text-gray-800">Web ãƒ«ãƒ¼ãƒ¬ãƒƒãƒˆ</h1>
        <p className="text-gray-500 mt-1">ã‚¯ãƒªãƒƒã‚¯ã§é‹è©¦ã—ï¼</p>
      </header>

      {/* ãƒ«ãƒ¼ãƒ¬ãƒƒãƒˆ */}
      <Roulette questions={questions} isSpinning={isSpinning} onStop={handleStop} />

      {/* ã‚¹ã‚¿ãƒ¼ãƒˆãƒœã‚¿ãƒ³ */}
      <button
        onClick={handleStart}
        disabled={isSpinning}
        className={`mt-10 px-10 py-3 text-lg font-bold rounded-full shadow-lg transition-all ${
          isSpinning
            ? "bg-gray-400 text-white cursor-not-allowed"
            : "bg-pink-500 hover:bg-pink-600 text-white"
        }`}
      >
        {isSpinning ? "å›è»¢ä¸­..." : "ã‚¯ãƒªãƒƒã‚¯ã—ã¦å›è»¢ã•ã›ã‚‹"}
      </button>

      {/* çµæœãƒ¢ãƒ¼ãƒ€ãƒ« */}
      <ResultModal
        isOpen={isResultOpen}
        question={result}
        onClose={() => setIsResultOpen(false)}
      />

      {/* è¨­å®šã‚¢ã‚¤ã‚³ãƒ³ï¼ˆãƒ€ãƒŸãƒ¼ï¼‰ */}
      <div className="absolute top-4 right-4">
        <SettingsIcon className="w-7 h-7 text-gray-400" />
      </div>
    </div>
  );
}
